/* PartMap Container Styling */
.part-map-container {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  font-family: 'DM Serif Text', 'Nunito', sans-serif;
  overflow: hidden;
  position: relative;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.5), 0 0 40px rgba(0, 0, 0, 0.3), 0 0 60px rgba(0, 0, 0, 0.2);
}

.part-map-container::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: url('../backgroundImages/istockphoto-160291870-1024x1024.jpg');
  background-size: cover;
  background-position: bottom center;
  background-repeat: no-repeat;
  opacity: 0.15;
  z-index: 0;
  pointer-events: none;
}

.part-map-container>* {
  z-index: 1;
}

.part-map-container .custom-montreal-map {
  width: 100%;
  height: 100vh;
  position: relative;
  background: transparent !important;
}

/* Ensure Leaflet container is transparent to show background image */
.part-map-container .leaflet-container {
  background: transparent !important;
  animation: none !important;
}

/* PartMap Styling - Shadow Effects for Part Separation */


/* Default shadow effects for PartMap only - border only changes on hover */
.part-map-container .leaflet-interactive {
  -webkit-filter: drop-shadow(1px 1px 0px rgba(255, 255, 255, 1)) drop-shadow(-1px -1px 0px rgba(255, 255, 255, 1)) drop-shadow(1px -1px 0px rgba(255, 255, 255, 1)) drop-shadow(-1px 1px 0px rgba(255, 255, 255, 1)) drop-shadow(0px 2px 4px rgba(0, 0, 0, 0.15));
  filter: drop-shadow(1px 1px 0px rgba(255, 255, 255, 1)) drop-shadow(-1px -1px 0px rgba(255, 255, 255, 1)) drop-shadow(1px -1px 0px rgba(255, 255, 255, 1)) drop-shadow(-1px 1px 0px rgba(255, 255, 255, 1)) drop-shadow(0px 2px 4px rgba(0, 0, 0, 0.15));
  transition: none;
  
}

.part-map-container .leaflet-interactive:hover,
.part-map-container .leaflet-interactive.part-hover {
  -webkit-filter: drop-shadow(1px 1px 0px rgba(0, 0, 0, 1)) drop-shadow(-1px -1px 0px rgba(0, 0, 0, 1)) drop-shadow(1px -1px 0px rgba(0, 0, 0, 1)) drop-shadow(-1px 1px 0px rgba(0, 0, 0, 1)) drop-shadow(0px 2px 4px rgba(0, 0, 0, 0.15));
  filter: drop-shadow(1px 1px 0px rgba(0, 0, 0, 1)) drop-shadow(-1px -1px 0px rgba(0, 0, 0, 1)) drop-shadow(1px -1px 0px rgba(0, 0, 0, 1)) drop-shadow(-1px 1px 0px rgba(0, 0, 0, 1)) drop-shadow(0px 2px 4px rgba(0, 0, 0, 0.15));
  transition: none;
  
}



/* Default shadow effects for PartMap only - extra thick white line separation + evenly distributed yellowish black shadow
.part-map-container .leaflet-interactive {
  filter:
    drop-shadow(2px 2px 0px rgba(255, 255, 255, 1)) drop-shadow(-2px -2px 0px rgba(255, 255, 255, 1)) drop-shadow(2px -2px 0px rgba(255, 255, 255, 1)) drop-shadow(-2px 2px 0px rgba(255, 255, 255, 1)) drop-shadow(0px 2px 0px rgba(255, 255, 255, 1)) drop-shadow(0px -2px 0px rgba(255, 255, 255, 1)) drop-shadow(2px 0px 0px rgba(255, 255, 255, 1)) drop-shadow(-2px 0px 0px rgba(255, 255, 255, 1)) drop-shadow(0px 0px 8px rgba(255, 215, 0, 0.4)) drop-shadow(0px 0px 16px rgba(0, 0, 0, 0.3)) drop-shadow(0px 0px 24px rgba(255, 215, 0, 0.2)) drop-shadow(0px 0px 32px rgba(0, 0, 0, 0.2));
  transition: none;
} */


/* Part-specific enhanced shadows */
.part-map-container .leaflet-overlay-pane svg path {
  transition: none;
  stroke-linejoin: round;
  stroke-linecap: round;
}

.pm-selected-part-label {
  position: relative;
  top: 2vh;
  width: 90vw;
  text-align: center;
  z-index: 2000;
  font-size: 36px;
}

/* Mobile - Add visible stroke to SVG paths and enhanced borders */
@media (max-width: 768px) {


  .pm-selected-part-label {
    position: relative;
    top: 2vh;
    width: 100vw;
    text-align: center;
    z-index: 2000;
    font-size: 24px;

  }

  /* Add stroke directly to SVG paths */
  .part-map-container .leaflet-overlay-pane svg path {
    stroke: #000000 !important;
    stroke-width: 4px !important;
    stroke-linejoin: round !important;
    stroke-linecap: round !important;
    paint-order: stroke fill !important;
  }

  /* Add white inner stroke for separation effect */
  .part-map-container .leaflet-interactive {
    -webkit-filter:
      drop-shadow(0px 0px 0px rgba(255, 255, 255, 1)) drop-shadow(1px 1px 0px rgba(255, 255, 255, 1)) drop-shadow(-1px -1px 0px rgba(255, 255, 255, 1)) drop-shadow(1px -1px 0px rgba(255, 255, 255, 1)) drop-shadow(-1px 1px 0px rgba(255, 255, 255, 1)) drop-shadow(2px 2px 0px rgba(0, 0, 0, 0.3)) !important;
    filter:
      drop-shadow(0px 0px 0px rgba(255, 255, 255, 1)) drop-shadow(1px 1px 0px rgba(255, 255, 255, 1)) drop-shadow(-1px -1px 0px rgba(255, 255, 255, 1)) drop-shadow(1px -1px 0px rgba(255, 255, 255, 1)) drop-shadow(-1px 1px 0px rgba(255, 255, 255, 1)) drop-shadow(2px 2px 0px rgba(0, 0, 0, 0.3)) !important;
    transition: none !important;
  }
}

/* Mobile Parts Navigation */
.mobile-parts-nav {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  background: linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 0.95) 20%, rgba(255, 255, 255, 1) 100%);
  -webkit-backdrop-filter: blur(10px);
  backdrop-filter: blur(10px);
  padding: 8px 8px 12px 8px;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 6px;
  z-index: 2000;
  box-shadow: 0 -2px 12px rgba(0, 0, 0, 0.1);
  border-top: 1px solid #FFD700;
}

.part-nav-button {
  background: linear-gradient(135deg, #FFD700 0%, #FFA500 100%);
  color: #000000;
  border: 1.5px solid #000000;
  border-radius: 8px;
  /* padding: 15px 10px; */
  font-family: 'DM Serif Text', 'Nunito', sans-serif;
  font-size: 10px;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 2px 8px rgba(255, 215, 0, 0.3);
  text-align: center;
  min-height: 36px;
  display: flex;
  align-items: center;
  justify-content: center;
  text-shadow: 0 1px 2px rgba(255, 255, 255, 0.5);
}

.part-nav-button:active {
  transform: scale(0.95);
  box-shadow: 0 2px 6px rgba(255, 215, 0, 0.6);
}

.part-nav-button:hover {
  background: linear-gradient(135deg, #FFA500 0%, #FFD700 100%);
  box-shadow: 0 6px 16px rgba(255, 215, 0, 0.6);
}



/* PartMap Mobile Responsive Styles */

/* Tablet and below - 768px */
@media (max-width: 768px) {
  .part-map-heading {

    font-size: 1.8rem !important;
    padding: 0 15px;
    width: 90%;
  }

  .part-map-legend {
    right: 15px !important;
    bottom: 80px !important;
    padding: 15px !important;
    min-width: 150px !important;
    max-width: 200px;
  }

  .part-map-legend h3 {
    font-size: 14px !important;
    margin: 0 0 10px 0 !important;
  }

  .legend-item {
    gap: 8px !important;
  }

  .legend-item>div {
    width: 20px !important;
    height: 20px !important;
  }

  .legend-item span {
    font-size: 12px !important;
  }

  .legend-hint {
    font-size: 11px !important;
    margin: 10px 0 0 0 !important;
  }

  /* Part label adjustments for tablet */
  .part-label-icon>div {
    font-size: 10px !important;
    letter-spacing: 1px !important;
  }
}

/* Mobile - 480px */
@media (max-width: 480px) {
  .part-map-heading {

    font-size: 1.4rem !important;
    padding: 0 10px;
    width: 95%;
  }

  .part-map-legend {
    right: 10px !important;
    bottom: 60px !important;
    padding: 12px !important;
    min-width: 130px !important;
    max-width: 160px;
  }

  .part-map-legend h3 {
    font-size: 13px !important;
    margin: 0 0 8px 0 !important;
  }

  .legend-item {
    gap: 6px !important;
  }

  .legend-item>div {
    width: 18px !important;
    height: 18px !important;
  }

  .legend-item span {
    font-size: 11px !important;
    line-height: 1.2;
  }

  .legend-hint {
    font-size: 10px !important;
    margin: 8px 0 0 0 !important;
  }

  /* Part label adjustments for mobile */
  .part-label-icon>div {
    font-size: 8px !important;
    letter-spacing: 0.5px !important;
  }
}

/* Small Mobile - 375px */
@media (max-width: 375px) {
  .part-map-heading {
    top: 8px !important;
    font-size: 1.2rem !important;
    padding: 0 8px;
  }

  .part-map-legend {
    right: 8px !important;
    bottom: 50px !important;
    padding: 10px !important;
    min-width: 120px !important;
    max-width: 140px;
  }

  .part-map-legend h3 {
    font-size: 12px !important;
    margin: 0 0 6px 0 !important;
  }

  .legend-item {
    gap: 5px !important;
  }

  .legend-item>div {
    width: 16px !important;
    height: 16px !important;
  }

  .legend-item span {
    font-size: 10px !important;
    line-height: 1.1;
  }

  .legend-hint {
    font-size: 9px !important;
    margin: 6px 0 0 0 !important;
  }

  /* Part label adjustments for small mobile */
  .part-label-icon>div {
    font-size: 7px !important;
    letter-spacing: 0.3px !important;
  }
}

/* Extra Small Mobile - 320px */
@media (max-width: 320px) {
  .part-map-heading {
    top: 5px !important;
    font-size: 1rem !important;
    padding: 0 5px;
  }

  .part-map-legend {
    right: 5px !important;
    bottom: 40px !important;
    padding: 8px !important;
    min-width: 110px !important;
    max-width: 130px;
  }

  .part-map-legend h3 {
    font-size: 11px !important;
    margin: 0 0 5px 0 !important;
  }

  .legend-item {
    gap: 4px !important;
  }

  .legend-item>div {
    width: 14px !important;
    height: 14px !important;
  }

  .legend-item span {
    font-size: 9px !important;
    line-height: 1.1;
  }

  .legend-hint {
    font-size: 8px !important;
    margin: 5px 0 0 0 !important;
  }

  /* Part label adjustments for extra small mobile */
  .part-label-icon>div {
    font-size: 6px !important;
    letter-spacing: 0.2px !important;
  }
}

/* Landscape mode optimizations */
@media (max-height: 500px) and (orientation: landscape) {
  .part-map-heading {
    top: 5px !important;
    font-size: 1.2rem !important;
  }

  .part-map-legend {
    bottom: 30px !important;
    padding: 8px !important;
    max-height: 60vh;
    overflow-y: auto;
  }

  .part-map-legend h3 {
    font-size: 12px !important;
    margin: 0 0 5px 0 !important;
  }

  .legend-item span {
    font-size: 10px !important;
  }

  .legend-hint {
    font-size: 9px !important;
  }
}